import { useState, useEffect, useRef } from 'react';
import "../styles/globals.css";

export default function game() {
  const [score, setScore] = useState(0);
  const [gameOver, setGameOver] = useState(false);
  const [lives, setLives] = useState(3);
  const [basketPosition, setBasketPosition] = useState(250);
  const [items, setItems] = useState([]);
  const gameContainerRef = useRef(null);


  // Game Logic - Start the game
  useEffect(() => {
    if (gameOver) return;

    // Add items to the screen every 1.5 seconds
    const itemInterval = setInterval(() => {
      if (gameOver) clearInterval(itemInterval);
      const randomX = Math.floor(Math.random() * 400);
      setItems((prevItems) => [
        ...prevItems,
        { x: randomX, y: 0, points: Math.floor(Math.random() * 10 + 1) },
      ]);
    }, 1500);

    // Move the falling items
    const gameInterval = setInterval(() => {
      if (gameOver) clearInterval(gameInterval);
      setItems((prevItems) => {
        const newItems = prevItems.map((item) => ({
          ...item,
          y: item.y + 5, // fall speed
        }));

        // Check for collisions with basket
        newItems.forEach((item) => {
          if (item.y > 400 && item.y < 430 && item.x >= basketPosition && item.x <= basketPosition + 50) {
            setScore((prevScore) => prevScore + item.points); // Increment score
            setItems((prevItems) => prevItems.filter((i) => i !== item)); // Remove item after catching
          }

          // Game Over if no more lives
          if (item.y > 450) {
            setLives((prevLives) => prevLives - 1);
            if (lives <= 1) setGameOver(true); // End game if no lives left
            setItems((prevItems) => prevItems.filter((i) => i !== item)); // Remove item after missing
          }
        });

        return newItems;
      });
    }, 50);

    return () => {
      clearInterval(itemInterval);
      clearInterval(gameInterval);
    };
  }, [basketPosition, gameOver, lives]);

  // Handle keyboard input to move the basket left and right
  const moveBasket = (e) => {
    if (gameOver) return;

    if (e.key === 'ArrowLeft' && basketPosition > 0) {
      setBasketPosition((prevPosition) => prevPosition - 10);
    } else if (e.key === 'ArrowRight' && basketPosition < 400) {
      setBasketPosition((prevPosition) => prevPosition + 10);
    }
  };

  useEffect(() => {
    window.addEventListener('keydown', moveBasket);

    return () => {
      window.removeEventListener('keydown', moveBasket);
    };
  }, [basketPosition]);

  // Function to restart the game
  const restartGame = () => {
    setScore(0);
    setLives(3);
    setGameOver(false);
    setItems([]);
    localStorage.setItem('gameScore', 0);  // Reset score in localStorage on restart
  };

  // Store score in localStorage when game ends
  useEffect(() => {
    if (gameOver) {
      localStorage.setItem('gameScore', score); // Store score in localStorage when game ends
    }
  }, [gameOver, score]);

  return (
    <div className="gameContainer" ref={gameContainerRef}>
      <h1>Catch the Item Game</h1>
      {gameOver ? (
        <div>
          <h2>Game Over!</h2>
          <h3>Final Score: {score}</h3>
          <button onClick={restartGame}>Restart Game</button>
        </div>
      ) : (
        <div>
          <h3>Score: {score}</h3>
          <h3>Lives: {lives}</h3>
        </div>
      )}
      <div className="gameArea">
        <div
          className="basket"
          style={{ left: `${basketPosition}px` }}
        ></div>

        {items.map((item, index) => (
          <div
            key={index}
            className="item"
            style={{
              left: `${item.x}px`,
              top: `${item.y}px`,
            }}
          >
            {item.points}
          </div>
        ))}
      </div>
    </div>
  );
}
